  box-sizing: border-box;
  cursor: pointer;
  padding: 0; }

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto; }

input[type="search"] {
  -webkit-appearance: textfield; }

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0; }

button:focus {
  outline: none; }

/*
  Textarea

  markup:
  <textarea name="" id="" cols="30" rows="10"></textarea>

  sg-wrapper:
  <div class="sg-body">
    <sg-wrapper-content/>
  </div>

  Styleguide 1.4.5
*/
textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical; }

/*
  Table Elements

  Styleguide 1.5
*/
/*
  Table

  markup:
  <table>
    <tr>
      <th>Table</th>
      <th>Head</th>
    <tr>
    <tr>
      <td>Table</td>
      <td>Cell</td>
    </tr>
  </table>

  sg-wrapper:
  <div class="sg-body">
    <sg-wrapper-content/>
  </div>

  Styleguide 1.5.1
*/
table {
  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0; }

